/* home.css */

.scrollable-background {
    background-image: url('../../public/logo512.png');
    background-repeat: repeat;
    background-size: cover;
    background-position: center;
    height: 500px;
    width: 500px;
    position: relative;
    overflow: hidden;
    z-index: -1;
    animation: slide 20s linear infinite;
}